<template>
    <div class="container">
        <div class="content">
            {{ base64Timestamp }}
        </div>
        
    </div>
</template>
<script>
export default{
    data(){
        return{

        }
    },
    computed: {
        base64Timestamp() {
            // 获取当前时间
            const now = new Date();
            // 获取当前小时数（0-23）
            const hours = now.getHours();
            // 判断当前时间是否超过晚上八点
            if (hours >= 20) {
                //已经超过晚上八点，那么需要重新获取新的密钥
                // 创建一个新的Date对象，表示当前时间加一天
                const tomorrow = new Date(now);
                tomorrow.setDate(now.getDate() + 1);
                // 将时间调整为08:00:00
                tomorrow.setHours(8, 0, 0, 0);
                // 获取时间戳（秒数）
                const timestampInSeconds = Math.floor(tomorrow.getTime() / 1000);
                // 将时间戳转换为字符串
                const timestampString = timestampInSeconds.toString();
                // 将字符串转换为Base64
                return btoa(timestampString);
            } else {
                //未超过晚上八点，那么就获取今天的
                //已经超过晚上八点，那么需要重新获取新的密钥
                // 创建一个新的Date对象，表示当前时间加一天
                const today = new Date(now);
                // 将时间调整为08:00:00
                today.setHours(8, 0, 0, 0);
                // 获取时间戳（秒数）
                const timestampInSeconds = Math.floor(today.getTime() / 1000);
                // 将时间戳转换为字符串
                const timestampString = timestampInSeconds.toString();
                // 将字符串转换为Base64
                return btoa(timestampString);
            }
        }
    }

}
</script>
<style scoped>
.content{
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 100vw;
    min-height: 100vh;
}
</style>